* {
  cursor: default; }

.hide {
  display: none; }

.show {
  display: block; }

.wrapper {
  margin: 0 auto;
  max-width: 1380px; }

.coverage {
  visibility: visible;
  top: 330px;
  transition: top 0.8s ease-out 0s; }

@media (max-width: 775px) {
  html, body {
    overflow-x: hidden; } }

.header {
  position: fixed;
  top: 0;
  font-family: 'Proxima Nova Rg';
  height: 150px;
  width: 100vw;
  display: flex;
  flex-flow: column nowrap;
  background-color: white;
  margin: 0 auto;
  max-width: 1380px;
  z-index: 10; }
  .header__top-block {
    height: 33%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 900;
    color: rgba(133, 51, 51, 0.8);
    border-bottom: 0.5px solid rgba(85, 85, 85, 0.8); }
  .header__foot-block {
    height: 67%;
    display: flex;
    flex-flow: row nowrap; }
  .header__logo-block {
    position: relative;
    width: 40%;
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center; }
  .header__logo {
    position: absolute;
    left: 100px;
    width: 35px;
    height: 35px;
    margin-right: 5px;
    transform: translateY(-35%); }
    .header__logo svg {
      fill: rgba(66, 66, 66, 0.9); }
  .header__title {
    position: absolute;
    left: 145px;
    font-size: 1.1em;
    color: rgba(55, 55, 55, 0.9);
    font-weight: 400; }
  .header__menu-block {
    position: relative;
    width: 60%;
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center; }

.menu {
  width: 90%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  align-items: center;
  position: absolute;
  top: 25px;
  left: 45px;
  color: rgba(54, 54, 54, 0.9);
  font-size: 0.92em;
  font-weight: 400; }
  .menu__item:hover {
    color: rgba(119, 119, 119, 0.9); }
  .menu__login {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between; }
    .menu__login_svg {
      width: 25px;
      height: 25px;
      margin-right: 10px; }
    .menu__login svg {
      fill: rgba(65, 65, 65, 0.9); }
    .menu__login_title {
      font-size: 18px;
      color: rgba(54, 54, 54, 0.9); }
  .menu__basket {
    display: flex;
    flex-flow: row nowrap; }
    .menu__basket_svg {
      width: 50px;
      height: 50px; }
    .menu__basket svg {
      fill: rgba(65, 65, 65, 0.9); }
    .menu__basket_counter {
      transform: translate(-160%, 27%);
      color: white; }

