.contact {
  position: relative;
  top: inherit;
  bottom: inherit;
  width: 100%;
  min-height: 850px;
  z-index: -5;
  font-family: 'Proxima Nova Rg'; }
  .contact__blocks {
    display: flex;
    flex-flow: row nowrap; }
  .contact__intro {
    height: 1300px; }
    .contact__intro0 {
      width: 35%;
      background-color: #ebe9e9;
      display: flex;
      flex-flow: column nowrap;
      justify-content: center;
      align-items: center; }
    .contact__intro1 {
      width: 65%;
      background-image: url(./assets/images/girl_contact_page.jpg);
      background-repeat: no-repeat;
      background-size: cover;
      margin-left: 10px; }
    .contact__intro_header {
      font-size: 17px;
      font-weight: 100;
      color: #7e7e7e;
      text-align: left;
      width: 75%;
      margin-bottom: 25px;
      text-decoration: underline; }
    .contact__intro_descrip {
      font-size: 15px;
      font-weight: 100;
      color: #7e7e7e;
      text-align: left;
      width: 75%;
      margin-bottom: 25px; }
  .contact__user-data-block {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: flex-start;
    width: 275px;
    padding: 0;
    margin: 0; }
  .contact__name-and-email {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    width: 100%; }
  .contact__input {
    background-color: transparent;
    border: none;
    border-bottom: 1px solid #7e7e7e;
    width: 49.5%;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center; }
    .contact__input:focus {
      border-bottom: 2px solid #414141; }
  .contact__comment {
    background-color: transparent;
    border: none;
    border-bottom: 1px solid #7e7e7e;
    width: 100%;
    height: 100px;
    resize: none;
    margin-top: 10px; }
    .contact__comment:focus {
      border-bottom: 2px solid #414141; }
  .contact__subbtn {
    border: none;
    width: 100%;
    height: 30px;
    margin-top: 10px;
    margin-bottom: 50px;
    color: white;
    background-color: #5c5c5c; }
    .contact__subbtn:hover {
      background-color: #848484; }

