* {
  cursor: default; }

.hide {
  display: none; }

.show {
  display: block; }

.wrapper {
  margin: 0 auto;
  max-width: 1380px; }

.coverage {
  visibility: visible;
  top: 330px;
  transition: top 0.8s ease-out 0s; }

@media (max-width: 775px) {
  html, body {
    overflow-x: hidden; } }

.tab {
  margin-top: 150px; }

.content {
  z-index: -5; }

@media (max-width: 775px) {
  .tab {
    margin-top: 0; } }
