* {
  box-sizing: border-box; }

.footer {
  position: relative;
  bottom: 0;
  width: 100%;
  min-height: 425px;
  height: 425px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  font-family: 'Proxima Nova Rg';
  border-top: 0.5px solid rgba(85, 85, 85, 0.8); }
  .footer__top-block {
    height: 80%;
    width: 100%;
    display: flex;
    flex-flow: row nowrap; }
  .footer__nav-block {
    height: 100%;
    width: 25%;
    display: flex;
    justify-content: center;
    flex-flow: column nowrap; }
  .footer__copyrights-block {
    height: 20%;
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    color: rgba(85, 85, 85, 0.8);
    border-top: 0.5px solid rgba(85, 85, 85, 0.8); }
  .footer__title {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    height: 20%;
    width: 100%;
    color: rgba(121, 46, 46, 0.9);
    font-size: 1.25em;
    font-weight: 600; }
  .footer__content {
    position: relative;
    height: 60%;
    width: 100%;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center; }
    .footer__content > ul {
      height: 100%;
      display: flex;
      flex-flow: column nowrap;
      justify-content: flex-start;
      align-items: center;
      padding-left: 0;
      margin-left: 0; }
  .footer__list {
    list-style-type: none;
    color: rgba(90, 90, 90, 0.9);
    margin-bottom: 10px; }
  .footer__email {
    position: relative;
    top: -25px;
    border: none;
    border-bottom: 1px solid black;
    margin-bottom: 25px;
    width: 200px;
    text-align: center;
    outline: none;
    box-sizing: border-box; }
    .footer__email:focus {
      border-bottom: 2px solid red; }
    .footer__email::placeholder {
      font-size: 0.9em;
      text-overflow: ellipsis;
      color: #5a5a5a; }
  .footer__button {
    width: 200px;
    position: relative;
    top: -25px; }

